import * as yup from "yup";
import {
  ADDRESS_LENGTH,
  CONTACT_INFORMATION_LENGTH,
  PHONE_NUMBER_LENGTH,
  TEXT_LENGTH,
  TITLE_LENGTH,
} from "./limits";

const phoneCodes = [
  "700",
  "701",
  "702",
  "703",
  "704",
  "705",
  "706",
  "707",
  "708",
  "709",
  "747",
  "750",
  "751",
  "760",
  "761",
  "762",
  "763",
  "764",
  "771",
  "775",
  "776",
  "777",
  "778",
];

export const orderSchema = yup.object().shape({
  title: yup
    .string()
    .max(TITLE_LENGTH, `  ${TITLE_LENGTH} `)
    .required(" "),
  totalSum: yup.mixed().test("Ok", "  ", (value) => {
    if (!value) {
      return true;
    }
    if (isNaN(value)) {
      return false;
    }
    return true;
  }),
  comment: yup.string(),
  description: yup
    .string()
    .typeError("    ")
    .max(TEXT_LENGTH),
});

export const supplierSchema = yup.object().shape({
  name: yup
    .string()
    .max(TITLE_LENGTH, `  ${TITLE_LENGTH} `)
    .required(" "),
  location: yup
    .string()
    .max(
      CONTACT_INFORMATION_LENGTH,
      `  ${CONTACT_INFORMATION_LENGTH} `
    )
    .required(" "),
  phoneNumber: yup
    .number()
    .typeError("   ")
    .min(PHONE_NUMBER_LENGTH, "   ")
    .required(" ")
    .test(
      "Ok",
      "       !",
      (value) => {
        const code = String(value).slice(-10).slice(0, 3);

        if (!phoneCodes.includes(code)) {
          return false;
        }
        return true;
      }
    ),
  description: yup
    .string()
    .typeError("    ")
    .max(TEXT_LENGTH, `  ${TEXT_LENGTH} `),
  address: yup
    .string()
    .max(ADDRESS_LENGTH, `  ${ADDRESS_LENGTH} `)
    .required(" "),
  contactName: yup
    .string()
    .max(TITLE_LENGTH, `  ${TITLE_LENGTH} `)
    .required(" "),
  webSite: yup
    .string()
    .max(
      CONTACT_INFORMATION_LENGTH,
      `  ${CONTACT_INFORMATION_LENGTH} `
    ),
  eMail: yup
    .string()
    .max(
      CONTACT_INFORMATION_LENGTH,
      `  ${CONTACT_INFORMATION_LENGTH} `
    )
    .email("  e-mail"),
});

export const otpSchema = yup.object().shape({
  phoneNumber: yup
    .number()
    .typeError("   ")
    .min(11, "   ")
    .required(" "),
  otp: yup
    .string()
    .required(" ")
    .matches(/^[0-9]+$/, "  -")
    .min(4, "  -")
    .max(4, "  -"),
});

export const customerSchema = yup.object().shape({
  name: yup
    .string()
    .max(TITLE_LENGTH, `  ${TITLE_LENGTH} `)
    .required(" "),
  location: yup
    .string()
    .max(
      CONTACT_INFORMATION_LENGTH,
      `  ${CONTACT_INFORMATION_LENGTH} `
    )
    .required(" "),
  contactName: yup
    .string()
    .max(
      CONTACT_INFORMATION_LENGTH,
      `  ${CONTACT_INFORMATION_LENGTH} `
    )
    .required(" "),
  phoneNumber: yup
    .number()
    .typeError("   ")
    .min(PHONE_NUMBER_LENGTH, "   ")
    .required(" ")
    .test(
      "Ok",
      "       !",
      (value) => {
        const code = String(value).slice(-10).slice(0, 3);

        if (!phoneCodes.includes(code)) {
          return false;
        }
        return true;
      }
    ),
});

export const categorySchema = yup.object().shape({
  name: yup
    .string()
    .max(TITLE_LENGTH, `  ${TITLE_LENGTH} `)
    .required(" "),
  parentId: yup
    .number()
    .typeError("  ")
    .required(" "),
});
